@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900&display=swap");

/*All project Colors */
:root {
  --primaryColor: hsl(14, 90%, 53%);
  --primaryColorLight: hsl(14, 96%, 91%);
  --bgColor: hsl(0, 0%, 100%);
  --formColor: hsl(20, 42%, 87%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyText: hsl(0, 0%, 51%);
  --inputColor: hsl(330, 12%, 97%);
  --cardColor: hsl(300, 3%, 94%);
  /* Font ang Typography */
  --biggestFontSize: 2.5rem;
  --h1FontSize: 1.6rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: 0.938rem;
  --smallFontSize: 0.813rem;
  --smallestFontSize: 0.75rem;
  --borderRadius: 3rem;
}
.portifolio {
  .secContainer {
    gap: 2rem;
    padding: 4rem 0 0;
    .leftContent {
      h3 {
        font-size: 23px;
        margin-bottom: 1rem;
      }
      .secHeading p {
        margin-bottom: 1rem;
        line-height: 1.5rem;
      }
      .iconDiv{
        .icon{
          color: var(--primaryColor);
        }
      }
      .grid {
        .singlePortofolio {
          gap: 1rem;
          align-items: flex-start;
          .iconDiv {
            img {
              height: 50px;
              width: 50px;
              margin-top: 1rem;
            }
          }
          .infor {
            h4 {
              padding: 0.6rem 0;
            }
          }
        }
      }
    }
    .rightContent {
      img {
        height: 200px;
        width: 200px;
        border-radius: 60px;
        /* align-items: center;
        justify-content: center; */
        margin:0 5rem ;
      }
    }
  }
}
@media screen and (max-width: 480px){
.portifolio{
  .secContainer{
    .leftContent{
      .iconDiv{
        .icon{
          margin-top: .5rem;
          font-size: 2.5rem;
        }
      }
    }
  }
}
}

 @media screen and (min-width: 480px) {
  .portifolio {
    .secContainer {
      .leftContent {
        .secHeading {
          h3 {
            font-size: var(--h2FontSize);
          }
          p {
            font: var(--normalFontSize);
            padding: 0.5rem 0;
          }
        }
        .grid {
          .singlePortofolio {
            gap: 1.5rem;
            .iconDiv {
              .icon {
                font-size: 4rem;
              }
            }
            .infor {
              padding-bottom: 0.5rem;
              font-size: var(--h3FontSize);
            }
            p {
              font: var(--normalFontSize);
            }
          }
        }
      }
    }
  }
}
@media screen and (min-width: 660px) {
  .portifolio {
    .secContainer {
      .leftContent {
        .secHeading {
          h3 {
            font-size: 2rem;
          }
          p {
            font-size: var(--h3FontSize);
          }
        }
        .grid {
          grid-template-columns: repeat(2, 1fr);
          .singlePortofolio {
            gap: 1.5rem;
          }
        }
      }
      .rightContent {
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          margin: auto;
          max-width: 80%;
        }
      }
    }
  }
}
@media screen and (min-width: 820px) {
  .portifolio {
    .secContainer {
      grid-template-columns: repeat(2, 1fr);
      .leftContent {
        h3 {
          font-size: var(--h2FontSize);
        }
        p {
          font-size: var(--h3FontSize);
        }
        .iconDiv{
          .icon{
            font-size: 5rem;
          }
        }
        .grid {
          grid-template-columns: repeat(1, 1fr);
          .singlePortofolio {
            gap: 1.5rem;
          }
        }
      }
      .rightContent {
        .iconDiv {
          .icon{
          max-width: 100%;
        }
        }
      }
    }
  }
}
@media screen and (min-width: 1240px) {
  .portifolio {
    .secContainer {
      gap: 3rem;
      .leftContent {
        h3 {
          font-size: 2rem;
        }
        p {
          font-size: var(--h3FontSize);
        }
        .iconDiv{
          .icon{
            font-size: 6rem;
          }
        }
      }
      .grid {
        gap: 2.5rem;
        margin-top: 3rem;
        .singlePortofolio {
          gap: 2rem;
        }
      }
    }
  }
} 
