@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900&display=swap");

/*All project Colors */
:root {
  --primaryColor: hsl(14, 90%, 53%);
  --primaryColorLight: hsl(14, 96%, 91%);
  --bgColor: hsl(0, 0%, 100%);
  --formColor: hsl(20, 42%, 87%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyText: hsl(0, 0%, 51%);
  --inputColor: hsl(330, 12%, 97%);
  --cardColor: hsl(300, 3%, 94%);
  /* Font ang Typography */
  --biggestFontSize: 2.5rem;
  --h1FontSize: 1.6rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: 0.938rem;
  --smallFontSize: 0.813rem;
  --smallestFontSize: 0.75rem;
  --borderRadius: 3rem;
}
.review {
  .secContainer {
    margin-bottom: -40px;
    .textDiv {
      .redText {
        font-size: 25px;
        display: block;
        color: var(--primaryColor);
        font-weight: 700;
        padding: 3rem 0 0;
      }
      h3 {
        font-size: var(--h2FontSize);
        color: var(--blackColor);
        padding: 1rem 0;
      }
      p {
        color: var(--blackColor);
      }
      .stars {
        gap: 0.4rem;
        padding: 1rem 0;
        .icon {
          color: var(--primaryColor);
          font-size: var(--h3FontSize);
        }
      }
      .clientsImages {
        gap: 1rem;
        img {
          height: 40px;
          width: 40px;
          border-radius: 50%;
          object-fit: cover;
          border: 2px solid rgb(255, 255, 255);
          box-shadow: 0 0 2px 2px rgb(251, 212, 194);
        }
      }
    }
    .imgDiv {
      height: 250px;
      width: 100%;
      border-radius: 10px;
      overflow: hidden;
      img {
        object-fit: cover;
      }
    }
  }
}
 @media screen and (min-width: 480px) {
  .review {
    .secContainer {
      .textDiv {
        .redText {
          font-size: var(--normalFontSize);
        }
        h3 {
          color: var(--blackColor);
          font-size: 20px;
        }
        p {
          color: var(--blackColor);
          font-size: var(--normalFontSize);
        }
        .stars {
          .icon {
            font-size: var(--h2FontSize);
          }
        }
        .clientsImages {
          img {
            height: 50px;
            width: 50px;
          }
        }
      }
      .imgDiv {
        height: 250px;
        width: 200px;
        img {
          height: 100px;
          width: 100%;
        }
      }
    }
  }
}
@media screen and (min-width: 740px) {
  .review {
    .secContainer {
      grid-template-columns: repeat(2, 1fr);
      .textDiv {
        .redText {
          font-size: var(--normalFontSize);
        }
        h3 {
          font-size: 20px;
          color: var(--blackColor);
        }
        p {
          color: var(--blackColor);
          font-size: var(--normalFontSize);
        }
        .stars {
          .icon {
            font-size: var(--h2FontSize);
          }
        }
        .clientsImages {
          img {
            height: 50px;
            width: 50px;
          }
        }
      }
      .imgDiv {
        height: 250px;
        width: 200px;
        margin-left: 6rem;
        margin-top: 8rem;
        img {
          height: 100px;
          width: 100%;
        }
      }
    }
  }
}
@media screen and (min-width: 1240px) {
  .review {
    .secContainer {
      gap: 3rem;
      .textDiv {
        .redText {
          font-size: var(--h3FontSize);
        }
        h3 {
          font-size: var(--h1FontSize);
          color: var(--blackColor);
        }
        p {
          font-size: var(--h3FontSize);
          color: var(--blackColor);
          line-height: 2rem;
        }
      }
      .imgDiv {
        img{
          height: 200px;
          object-fit: cover;
        }
      }
    }
  }
} 
