@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900&display=swap");
/*All project Colors */
:root {
  --primaryColor: hsl(14, 90%, 53%);
  --primaryColorLight: hsl(14, 96%, 91%);
  --bgColor: hsl(0, 0%, 100%);
  --formColor: hsl(20, 42%, 87%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyText: hsl(0, 0%, 51%);
  --inputColor: hsl(330, 12%, 97%);
  --cardColor: hsl(300, 3%, 94%);
  /* Font ang Typography */
  --biggestFontSize: 2.5rem;
  --h1FontSize: 1.6rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: 0.938rem;
  --smallFontSize: 0.813rem;
  --smallestFontSize: 0.75rem;
  --borderRadius: 3rem;
}
.accordionContainer {
  .title {
    font-size: var(--normalFontSize);
    align-items: center;
    padding: 1rem;
    border-radius: 10px;
    justify-content: space-between;
    background-color: var(--primaryColorLight);
    margin: 1rem;
    /* margin: 1rem 0 0 1.4rem; */
    span {
      cursor: pointer;
      .icon {
        font-size: 1.2rem;
        color: var(--blackColor);
      }
    }
  }
  .description {
    font-size: var(--smallFontSize);
    display: flex;
    align-items: center;
    line-height: 20px;
    white-space: pre-line;
    height: 0;
    transition: all 0.3s ease-in-out;
    opacity: 0;
    padding: 0 1rem;
    margin: 0 1rem;
    background: var(--cardColor);
    border-radius: 0 0 10px 10px;
    color: var(--blackColor);
  }
  .show {
    height: 180px;
    opacity: 1;
  }
  .activeTitle {
    background: var(--primaryColorLight);
    border-radius: 10px 10px 0 0;
  }
}
.questions {
  .secHeading {
    h3,
    h4 {
      padding: 1rem;
    }
    p {
      padding: 1rem;
      font-size: var(--smallFontSize);
      color: var(--blackColor);
    }
  }
  .form {
    .formContent {
      gap: 1.5rem;
      input,
      textarea {
        background-color: var(--cardColor);
        outline: none;
        padding: 1rem;
        margin: 0 1rem;
        border: none;
        border-radius: var(--borderRadius);
        &::placeholder {
          font-size: var(--smalltFontSize);
          color: var(--blackColor);
        }
        textarea {
          height: 100px;
          border: 10px;
        }
      }
      .quest {
        margin: 0 1rem;
      }
    }
  }
}

@media screen and (min-width: 640px) {
  .questions {
    .accordionContainer {
      .title {
        font-size: var(--h3FontSize);
        span {
          .icon {
            font-size: 1.5rem;
          }
        }
      }
      .description {
        font-size: var(--normalFontSize);
      }
      .show {
        height: 150px;
      }
    }
  }
  .questions {
    .secHeading {
      h3,
      h4 {
        font-size: var(--h2FontSize);
      }
      p {
        font-size: var(--normalFontSize);
      }
    }
    .secContainer {
      grid-template-columns: repeat(2, 1fr);
      align-items: flex-start;
      gap: 2rem;
      .form {
        .formContent {
          input,
          textarea {
            font-size: var(--normalFontSize);
            &::placeholder {
              font-size: var(--normalFontSize);
              color: var(--blackColor);
            }
          }
          textarea{
            height: 100px;
            border-radius: 10px;
          }
        }
      }
    }
  }
}
