@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900&display=swap");

/*All project Colors */
:root {
  --primaryColor: hsl(14, 90%, 53%);
  --primaryColorLight: hsl(14, 96%, 91%);
  --bgColor: hsl(0, 0%, 100%);
  --formColor: hsl(20, 42%, 87%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyText: hsl(0, 0%, 51%);
  --inputColor: hsl(330, 12%, 97%);
  --cardColor: hsl(300, 3%, 94%);
  /* Font ang Typography */
  --biggestFontSize: 2.5rem;
  --h1FontSize: 2rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: 0.938rem;
  --smallFontSize: 0.813rem;
  --smallestFontSize: 0.75rem;
  --borderRadius: 3rem;
}
.navBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem 2rem;
  width: 100%;
  position: fixed;
  border-bottom: 1.5px solid rgba(211, 211, 211);
  backdrop-filter: blur(5px);
  z-index: 1000;
  background: rgba(255,255,255,0.737);
  .logoDiv {
    display: flex;
    align-items: center;
    justify-content: center;
    span {
      color: var(--blackColor);
      font-size: var(--h3FontSize);
      justify-content: center;
    }
  }
}
   /*

/* Media queries */
 /*    */
 /*   */
 /*  */
@media screen and (max-width: 766px) {
  .menue {
    position: absolute;
    width: 70%;
    top: -100rem;
    right: 50%;
    left: 50%;
    transform: translate(-50%);
    padding: 2rem;
    background-color: rgba(255, 255, 255, 0.94);
    border-radius: 10px;
    transition: 0.8s ease-in-out;
    backdrop-filter: blur(5px);
    box-shadow: 0 2px 3px 4px rgba(236, 213, 213, 0.516);
  }
  li {
    font-weight: 500;
    text-align: center;
    padding: 1rem 0;
    font-size: var(--h3FontSize);
    cursor: pointer;
    transition: 0.2s;
    &:hover {
      color: var(--primaryColor);
      border-bottom: 1.5px solid var(--primaryColor);
    }
  }
}
.closeMenu,
.menuIcon {
  font-size: var(--h1FontSize);
}
.closeMenu:hover, .menuIcon:hover{
  color: var(--primaryColor);
}
.closeMenu{
  position: absolute;
  bottom: -15px;
  left: 50%;
  right: 50%;
  transform: translate(-50%);
  color: var(--blackColor);
}
.shownavbar{
  top:8rem;
} 
@media screen and (min-width: 1440px) {
  .navBar{
    padding: 2rem 14rem;
  }
}
@media screen and (min-width: 1240px){
  .navBar{
    padding: 2rem 6rem;
    .menue{
      ul{
        gap: 4rem;
      }
    }
  }
} 
@media screen and (min-width: 767px) {
  .navBar{
    .logoDiv{
      span{
        font-size: var(--h2FontSize);
      }
    }
    .closeMenu,.menuIcon{
      display: none;
    }
    .menue{
      ul{
        display: flex;
        gap: 1rem;
        li{
          font-size: var(--h3FontSize);
          font-weight: 500;
          padding: 1rem 0;
          text-align: center;
          cursor: pointer;
          transition: .3s ease-in;
          &:hover{
            color: var(--primaryColor);
            border-bottom: 1.5px solid var(--primaryColor);
          }
        }
      }
    }
  }
  
  }