@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900&display=swap");

/*All project Colors */
:root {
  --primaryColor: hsl(14, 90%, 53%);
  --primaryColorLight: hsl(14, 96%, 91%);
  --bgColor: hsl(0, 0%, 100%);
  --formColor: hsl(20, 42%, 87%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyText: hsl(0, 0%, 51%);
  --inputColor: hsl(330, 12%, 97%);
  --cardColor: hsl(300, 3%, 94%);
  /* Font ang Typography */
  --biggestFontSize: 2.5rem;
  --h1FontSize: 2rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: 0.938rem;
  --smallFontSize: 0.813rem;
  --smallestFontSize: 0.75rem;
  --borderRadius: 3rem;
}
.Home {
  position: relative;
  height: 95vh;
  top: 0;
  width: 100%;
  .videoBg {
    position: absolute;
    height: 100%;
    width: 100%;
    video {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  .sectionText {
    position: absolute;
    top: 30%;
    bottom: 50%;
    height: 100%;
    padding: 0 1.3rem;
    color: var(--whiteColor);
  }
  h1 {
    font-size: var(--biggestFontSize);
    font-weight: 900;
  }
  p {
    font-size: var(--normalFontSize);
    font-weight: 500;
    padding: 1rem 0;
  }
  .btn {
    padding-left: 2rem;
    padding-right: 2rem;
    font-size: var(--h3FontSize);
  }
}
 @media screen and (min-width: 480px) {
  .Home {
    .sectionText {
      padding: 0 2rem;
      justify-content: center;
      text-align: center;
      width: 100%;
      h1 {
        font-size: var(--biggestFontSize);
        font-weight: 900;
      }
      p {
        padding: 1rem 0;
        font-size: var(--h2FontSize);
        max-width: 70%;
        margin: auto;
      }
      .btn {
        font-size: var(--h3FontSize);
        margin: auto;
      }
    }
  }
}
@media screen and (min-width: 767px) {
  .Home {
    .sectionText {
      margin-top: 6rem;
      h1 {
        font-size: 3rem;
        font-weight: 900;
        max-width: 70%;
        margin: auto;
      }
      p {
        padding: 1rem 0;
        font-size: var(--h2FontSize);
        max-width: 70%;
        margin: auto;
      }
      .btn {
        gap: 0.5rem;
        padding-left: 2rem;
        padding-right: 2rem;
        font-size: var(--h3FontSize);
        margin: auto;
      }

    }
    .popularPlaces {
      display: none;
    }
  }
}
@media screen and (min-width: 1040px) {
  .Home {
    position: relative;
    .popularPlaces {
      display: block;
      position: absolute;
      bottom: 10%;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      .content {
        h3 {
          color: var(--whiteColor);
          padding: 1rem 0;
          position: relative;
          &::after {
            content: "";
            width: 83%;
            height: 2px;
            background: rgb(202, 202, 202);
            right: 0;
            bottom: 50%;
            position: absolute;
          }
        }
        .images {
          align-items: center;
          justify-content: center;
          gap: 2rem;
        }
        img {
          height: 125px;
          width: 125px;
          border-radius: 15px;
        }
      }
    }
  }
} 
