@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900&display=swap");

/*All project Colors */
:root {
  --primaryColor: hsl(14, 90%, 53%);
  --primaryColorLight: hsl(14, 96%, 91%);
  --bgColor: hsl(0, 0%, 100%);
  --formColor: hsl(20, 42%, 87%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyText: hsl(0, 0%, 51%);
  --inputColor: hsl(330, 12%, 97%);
  --cardColor: hsl(300, 3%, 94%);
  /* Font ang Typography */
  --biggestFontSize: 2.5rem;
  --h1FontSize: 1.6rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: 0.938rem;
  --smallFontSize: 0.813rem;
  --smallestFontSize: 0.75rem;
  --borderRadius: 3rem;
}

.destinations {
  .secContainer {
    .redText {
      color: var(--primaryColor);
      font-size: 25px;
      font-weight: 500;
    }
    h3 {
      font-size: var(--h1FontSize);
      padding: 1rem 0;
    }
    .searchField {
      margin-top: 2rem;
      gap: 1rem;
      .inputField {
        background: var(--primaryColorLight);
        padding: 1rem;
        border-radius: var(--borderRadius);
        gap: 0.5rem;
      }
      input {
        background: transparent;
        outline: none;
        border: none;
        width: 100%;
      }
      .icon {
        color: var(--blackColor);
      }
    }
    .btn {
      gap: 0.5rem;
      align-items: center;
      justify-content: center;
      margin: auto;
      width: 100%;
      letter-spacing: 0.2rem;
    }
  }
}


.secMenu {
  display: none;
}
.destinationContainer {
  margin-top: 2rem;
  .singleDistination {
    position: relative;
    overflow: hidden;
    border-radius: 1rem;
    height: 260px;
    .imgDiv {
      position: relative;
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: all 0.3s ease-in-out;
      }
      img:hover {
        scale: 1.3;
      }
    }
    .descInfo {
      position: absolute;
      z-index: 2;
      bottom: 0;
      padding: 1rem 2rem 1rem;
      width: 100%;
      justify-content: space-between;
      &::after {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background: linear-gradient(
          to bottom,
          rgba(0, 0, 0, 0),
          rgba(0, 0, 0, 0.76)
        );
        z-index: -1;
      }
      .text {
        gap: 0.5rem;
        color: var(--whiteColor);
        .name {
          display: block;
          font-weight: 500;
          padding-bottom: 0.3rem;
          font-size: var(--h2FontSize);
        }
        .flex .icon {
          font-size: var(--normalFontSize);
        }
      }
      .rating {
        padding: 8px 1rem;
        border-radius: var(--borderRadius);
        background: rgba(245, 245, 245, 0.37);
        border: 1.5px solid rgba(255, 255, 255, 0.241);
        font-weight: 600;
        color: var(--whiteColor);
      }
    }
  }
}

@media screen and (min-width: 520px) {
  .destinations {
    .secContainer {
      .searchField {
        grid-template-columns: repeat(2, 1fr);
      }
      .destinationContainer{
        grid-template-columns: repeat(2,1fr);
      }
    }
  }
} 
 @media screen and (min-width: 640px) {
  .destinations {
    .secContainer {
      .secMenu{
        display: block;
        .flex{
          width: 100%;
          margin: 2rem auto;
          justify-content: center;
          gap: 1rem;
          li{
            background: transparent;
            padding: 10px 18px;
            border-radius: var(--borderRadius);
            color: var(--blackColor);
            border: 1.5px dashed transparent;
            cursor: pointer;
            transition: .2s;
          }
          li:hover{
            color: var(--primaryColor);
            border: 1.5px dashed var(--primaryColor);
          }
          .active{
            color: var(--primaryColor);
            border: 1.5px dashed var(--primaryColor);
          }
        }
      }

    }
  }
} 
 @media screen and (min-width: 1014px) {
  .destinations {
    .secContainer {
      .secTitle{
        .redText{
          font-size: var(--normalFontSize);
        }
        h3{
          font-size: var(--h1FontSize);
        }
        p{
          font-size: var(--h3FontSize);
        }
      }
      .secMenu{
        .flex{
          justify-content: left;
          li{
            background: transparent;
            padding: 10px 18px;
            border-radius: var(--borderRadius);
            color: var(--blackColor);
            border: 1.5px dashed transparent;
            cursor: pointer;
            transition: .2s;
          }
          li:hover{
            color: var(--primaryColor);
            border: 1.5px dashed var(--primaryColor);
          }
        }
      }

    }
  }
}

 @media screen and (min-width: 1014px) {
  .destinations {
    .secContainer {
      .searchField {
        grid-template-columns: repeat(4, 1fr);
      }
      .destinationContainer{
        grid-template-columns: repeat(3,1fr);
      }
    }
  }
} 
 @media screen and (min-width: 1400px) {
  .destinations {
    .secContainer {

      .destinationContainer{
        grid-template-columns: repeat(4,1fr);
      }
    }
  }
} 
